/* eslint-disable react/display-name */
import React from 'react';
import { ComboBox, TextBox } from './EditableComponents';

const components = {
  DSComboBox: ComboBox,
  DSTextBox: TextBox,
};

const getEditorComponent = (editorComponent = {}, handlers) => ({
  value,
  onValue,
  extraParameters,
}) => {
  if (typeof editorComponent === 'function') {
    return editorComponent({
      value,
      onValue,
      customHandlers: handlers,
      ...extraParameters,
    });
  }

  const { type = 'DSTextBox', ...restEditorProps } = editorComponent;
  const Component = components[type];

  if (type === 'DSComboBox') {
    // TODO: remove this whole condition when combobox v3 is out and fully working without "maxOptions" parameter
    const maxOptions = restEditorProps?.options?.length || 100;
    return (
      <div style={{ zIndex: 0 }}>
        <Component
          autoFocus
          onValue={onValue}
          value={value}
          maxOptions={maxOptions}
          {...restEditorProps}
        />
      </div>
    );
  }

  return (
    <Component autoFocus onValue={onValue} value={value} {...restEditorProps} />
  );
};

export default getEditorComponent;
