/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable react/prop-types */
/* eslint-disable jsx-a11y/no-noninteractive-tabindex */
import React, { useContext, useRef } from 'react';
import { components } from 'react-select';
import { cx } from '@elliemae/ds-utilities/utils';
import { DropDownContext } from '../context';

export function ValueContainer({ children, ...rest }) {
  const valueContainerRef = useRef(null);
  const {
    changeMenuOpen,
    inputInFocus,
    value,
    clearable,
    removeDropdownIndicator,
  } = useContext(DropDownContext);

  return (
    <div
      data-testid="combobox-value-container"
      onClick={(e) => {
        e.stopPropagation();
        changeMenuOpen(true);
      }}
      ref={valueContainerRef}
      className={cx(
        'combobox-v2-value-container',
        inputInFocus && 'in-focus',
        value && clearable && 'has-value',
        removeDropdownIndicator && 'dropdown-indicator-removed',
      )}
    >
      <components.ValueContainer {...rest}>
        {children}
      </components.ValueContainer>
    </div>
  );
}

export default ValueContainer;
